<template>
  <container :title="$t('admin.title')">
    <div class="row">
      <div class="col-lg-3 col-xs-6" v-for="(item,index) in smallBoxes" :key="index">
        <small-box v-bind="item"/>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-7 col-sm-7 col-xs-12">
        <Box v-bind="systemPost"/>
      </div>
      <div class="col-lg-5 col-sm-5 col-xs-12">
        <Box v-bind="loginLogPost"/>
      </div>
    </div>
  </container>
</template>

<script>
    import {SmallBox,Box,Container} from "@/components"
    import _ from 'lodash'
    import {mapState} from 'vuex'

    export default {
      name: "index",
      components: {Container, Box, SmallBox},
      mounted(){
        this.$store.dispatch('admin/fetchData')
      },
      computed:{
        ...mapState('admin',{
          user_total: state => state.user_total,
          article_total: state => state.article_total,
          user_near_num: state => state.user_near_num,
          comment_near_num: state => state.comment_near_num,
          login_log_list: state => state.login_log_list,
          current_time: state => state.server_current_time
        }),
        smallBoxes(){
          return [
            { title:'user',number:this.user_total,url:'user_list' },
            { title:'article',number:this.article_total,url:'article_list' },
            { title:'userNear',number:this.user_near_num,url:'user_list' },
            { title:'comment',number:this.comment_near_num,url:'leave_word_list' },
          ]
        },
        loginLogPost(){
          let data = []
          this.login_log_list.forEach((vol) =>{
            const tmp = {
              ip: '<span class="label label-success">'+vol.ip+'</span>',
              location: vol.country+' '+vol.province+ ' '+ vol.city+ ' ' + vol['district'],
              login_time: vol.login_time
            }
            data.push(tmp)
          })
          return {
            title:'最新登录',
            data,
            columns:['IP','位置','登录时间']
          }
        },
        systemPost(){
          const config = this.$t('admin.sys_config')
          const copy_config = _.cloneDeep(config)
          copy_config.data.server = config.data.server + this.current_time
          return copy_config
        }
      }
    }
</script>



